<template>
  <a-scene  renderer="colorManagement: true" >
    <a-assets>
      <a-asset-item  id="house_0" src="/casa_0.glb"></a-asset-item>
    </a-assets>
   
    <a-entity camera look-controls wasd-controls position="0 1.5 1">
    </a-entity>
    <a-entity id="main_model" gltf-model="url(/model_main.glb)" position="0 0 0" scale=".3 .3 .3" initial_screen></a-entity>


    <!--house models-->
    <a-entity id="casa_0" gltf-model="url(/casa_0.glb)" position="0 0 0" scale="1 1 1" data-raycastable v-if="show_1===true"></a-entity>
    <a-entity id="casa_1" gltf-model="url(/casa_1.glb)" position="0 0 0" scale="1 1 1" data-raycastable v-if="show_2===true"></a-entity>
    <a-entity id="casa_2" gltf-model="url(/casa_2.glb)" position="0 0 0" rotation="0 -180 0" scale="1 1 1" data-raycastable v-if="show_3===true"></a-entity>
    <!--house models-->

    <a-sky src="/360.jpg" rotation="0 0 0"></a-sky>
        <a-entity 
        id="mouse_cursor"
        cursor="rayOrigin:mouse"
        raycaster="objects: [data-raycastable]"
        ></a-entity>


    <CityModalComponent/>
    <PlaceModalComponent/>
    <PriceModalComponent/>


    <!--<HomeViewer/>-->

        <a-entity 
        laser-controls="hand:left;"
        raycaster="lineColor: red;objects: [data-raycastable]; lineOpacity: 0.5"
        control-left
        go-back
        ></a-entity>

        <a-entity 
        laser-controls="hand:right;"
        raycaster="lineColor: red;objects: [data-raycastable]; lineOpacity: 0.5"
        control-right
        ></a-entity>

        
        <HyperComponent text="Volver" v-if="show_1 === true || show_2===true || show_3 ===true"
        @click="hideAll"
        position="0 .9 -1"/>
        

    <a-entity id="info_panel" geometry="primitive:plane" scale=".45 .25" rotation="-45 0 0" material="color:#2DA4B2;transparent:true;opacity:.6">
      <a-troika-text scale=".35 .5"  :value="`${textValue}`" color="#fff" position=".00 0 .001"></a-troika-text>
    </a-entity>
    
    <!--FIRST ROW-->
    <MainCardComponent 
        :position="'-1.4 1.8 -1.8'"
        :scale="scale_1" 
        :rotation="'0 7.5 0'"
        :material_all="mainStore.image_0"
        ref="box_1"
        @click="mouseClick('box_1', this.$refs.box_1)"
        @mouseenter="mouseOver('box_1', this.$refs.box_2,0)"
        @mouseleave="mouseOut('box_1', this.$refs.box_2)"
        />

    <MainCardComponent 
        :position="'0 1.8 -1.85'"
        :scale="scale_2" 
        :rotation="'0 0 0'"
        :material_all="mainStore.image_1"
        ref="box_2"
        @click="mouseClick('box_2', this.$refs.box_2)"
        @mouseenter="mouseOver('box_2', this.$refs.box_2,1)"
        @mouseleave="mouseOut('box_2', this.$refs.box_2)"
        />
        
    <MainCardComponent 
        :position="'1.4 1.8 -1.8'"
        :scale="scale_3" 
        :rotation="'0 -7.5 0'"
        :material_all="mainStore.image_2"
        ref="box_3"
        @click="mouseClick('box_3', this.$refs.box_2)"
        @mouseenter="mouseOver('box_3', this.$refs.box_2,2)"
        @mouseleave="mouseOut('box_3', this.$refs.box_2)"
       />

    



  </a-scene>

</template>



<script>
import {useMainStore} from './stores/mainStore';
import HelloWorld from './components/HelloWorld.vue';
import HyperComponent from './components/HyperComponent.vue';
import MainCardComponent from './components/MainCardComponent.vue';
import CityModalComponent from './components/CityModalComponent.vue';
import PlaceModalComponent from './components/PlaceModalComponent.vue';
import PriceModalComponent from './components/PriceModalComponent.vue';
import HomeViewer from './components/HomeViewer.vue';

import data from '@/data/data.json';
import AFRAME from 'aframe';


function radToDeg(radians){
  var pi = Math.PI;
  return radians * (180/pi);
}

export default {
  name: 'App',
  components: {
    HelloWorld,
    MainCardComponent,
    HyperComponent,
    CityModalComponent,
    PlaceModalComponent,
    PriceModalComponent,
    HomeViewer
},
  setup(){
    const mainStore = useMainStore();
    return{
      mainStore
    }
  },
  data: function () {
    return {
      data:data,
      isClicked: false,
      offset:'0 0 0',
      textValue: '',
      textValue2:'',
      scale_1: '1.25 .8 .05',
      scale_2: '1.25 .8 .05',
      scale_3: '1.25 .8 .05',
      show_1:false,
      show_2:false,
      show_3:false
    }
  },
  methods:{
    hideAll: function(){
      document.getElementById('main_model').setAttribute('scale','.3 .3 .3');
      this.show_1 = false;
      this.show_2 = false;
      this.show_3 = false;
    },
    mouseClick: function(type,element){
      this.mainStore.currentClickedBox = type;  
      console.log(this.mainStore.currentClickedBox);
      //this.textValue = 'value: '+this.mainStore.currentClickedBox;

      if(type === 'box_1'){
        this.show_1 = true;
        document.getElementById('main_model').setAttribute('scale','0 0 0');
        this.show_2 = false;
        this.show_3 = false;
        
      }
      else if(type==='box_2'){
        this.show_2 = true;
        document.getElementById('main_model').setAttribute('scale','0 0 0');
        this.show_1 = false;
        this.show_3 = false;
        
      }
      else if(type==='box_3'){
        this.show_3 = true;
        document.getElementById('main_model').setAttribute('scale','0 0 0');
        this.show_1 = false;
        this.show_2 = false;
        
      }
    },
    mouseOver: function(type,element,index){
      this.textValue = data.houses.valladolid[index].name + '\n' +data.houses.valladolid[index].address + '\n' + data.houses.valladolid[index].price + '\n' + data.houses.valladolid[index].sqm;
    

      if(type === 'box_1'){
        this.scale_1 = '1.3 .9 .05';
      }
      else if(type==='box_2'){
        this.scale_2 = '1.3 .9 .05'
      }
      else if(type==='box_3'){
        this.scale_3 = '1.3 .9 .05'
      }

    },
    mouseOut: function(type,element){
      //element.setAttribute('scale','.8 .8 .25');
      //this.textValue = 'value: MouseOut ' + type;

      if(type === 'box_1'){
        this.scale_1 = '1.25 .8 .05'
      }
      else if(type==='box_2'){
        this.scale_2 = '1.25 .8 .05'
      }
      else if(type==='box_3'){
        this.scale_3 = '1.25 .8 .05'
      }
    }
  },
  mounted(){

      AFRAME.registerComponent('control-right',{
        init: function(){

        },
        tick: function(){
          document.getElementById('info_panel').setAttribute('position',`${this.el.object3D.position.x-.05} ${this.el.object3D.position.y} ${this.el.object3D.position.z+.15}`)
          document.getElementById('info_panel').setAttribute('rotation',`${-30} ${0} ${0}`)
          
        }
      })

      AFRAME.registerComponent('go-back',{
        init: function(){

        },
        tick: function(){
        
          //document.getElementById('close').setAttribute('position',`${this.el.object3D.position.x} ${this.el.object3D.position.y} ${this.el.object3D.position.z-2}`);
          //document.getElementById('close').setAttribute('rotation',`${radToDeg(this.el.object3D.rotation.x)} ${radToDeg(this.el.object3D.rotation.y)} ${radToDeg(this.el.object3D.rotation.z)}`)
        }
      })
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
