<template >
    <a-entity geometry="primitive:plane" scale="2 2" position="0 2 -1.8" material="color:#2D496C" v-if="mainStore.showPrice === true" data-raycastable>

        <a-circle 
        position=".45 .45 .02" 
        scale=".08 .08" 
        material="color:red"
        data-raycastable
        @click="closeModal">
        </a-circle>

        <a-entity 
        geometry="primitive:plane"
        scale=".3 .1" 
        position="0 0.3 .02"
        :material="`src:url(${bg_gradient})`"
        @click="selectPrice('<50K')"
        data-raycastable>
        <a-troika-text
        value="<50K€"
        position="0 -.05 .01"
        scale="1 2.5 1">
        </a-troika-text>
        </a-entity>

        <a-entity 
        geometry="primitive:plane"
        scale=".3 .1" 
        position="0 0.1 .02"
        :material="`src:url(${bg_gradient})`"
        @click="selectPrice('>100K')"
        data-raycastable>
        <a-troika-text
        value=">100K €"
        position="0 -.05 .01"
        scale="1 2.5 1">
        </a-troika-text>
        </a-entity>

        <a-entity 
        geometry="primitive:plane"
        scale=".3 .1" 
        position="0 -.1 .02"
        :material="`src:url(${bg_gradient})`"
        @click="selectPrice('>500K')"
        data-raycastable>
        <a-troika-text
        value=">500K €"
        position="0 -.05 .01"
        scale="1 2.5 1">
        </a-troika-text>
        </a-entity>

        <a-entity 
        geometry="primitive:plane"
        scale=".3 .1" 
        position="0 -.3 .02"
        :material="`src:url(${bg_gradient})`"
        @click="selectPrice('>1M')"
        data-raycastable>
        <a-troika-text
        value=">1M €"
        position="0 .05 .01"
        scale="1 2.5 1">
        </a-troika-text>
        </a-entity>



    </a-entity>
</template>

<script>
import { useMainStore } from '@/stores/mainStore';
import bg_gradient from '@/assets/bg_gradient.png';
    export default{
        name:"PriceModalComponent",
        setup(){
            const mainStore = useMainStore();
            return {
                mainStore
            }
        },
        data:function(){
            return{
                scale:'.3 .1',
                bg_gradient: bg_gradient
            }
        },
        methods:{
            selectPrice:function(city){
                this.mainStore.currentPrice = city
            },
            closeModal: function(){
                this.mainStore.showPrice = false;
            }
        }
    }
</script>

