<template>
    <a-entity id="main_card" :scale="scale" :position="position" :rotation="rotation" geometry="primitive: box" material="color:#072146" data-raycastable>
        <a-plane scale=".8 .8" position="0 0 0.55" :material="`src:${material_all}`">
        </a-plane>
    </a-entity>
</template>

<script>
import { useMainStore } from '@/stores/mainStore';

export default{
    name:'MainCardComponent',
    props:{
        material_all: String
    },
    setup(){
        const mainStore = useMainStore();
        return {
            mainStore
        }
    },
    data: function () {
    return {
      position: '0 0 0',
      scale:'0 0 0',
      rotation: '0 0 0'
    }
  },
}
</script>

